* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* Configuration de base */
.App {
  text-align: center;
}

/* Configuration des diapositives */
.mySwiper {
  width: 100%;
  height: 95vh;
  display: flex;
}

main {
  width: 100%;
  height: 95vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
}

.slide1, .slide2, .slide3 {
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  height: 95vh;
  background: linear-gradient(to bottom, rgba(117, 117, 117, 0.15), rgba(32, 32, 32, 0)),
    url("../Images/image2.jpg");
}

.slide2 {
  background: linear-gradient(to bottom, rgba(117, 117, 117, 0.15), rgba(32, 32, 32, 0)),
    url("../Images/image4.jpg");
}

.slide3 {
  background: linear-gradient(to bottom, rgba(117, 117, 117, 0.15), rgba(32, 32, 32, 0)),
    url("../Images/image5.jpg");
}

/* Boutons du swiper */
.mySwiper .swiper-button-next,
.mySwiper .swiper-button-prev {
  background-color: rgba(26, 26, 26, 0.8);
  padding: 15px;
  color: #f9f9f9 !important;
}

/* Section principale */
main .placement {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(145, 145, 145, 0.35);
  backdrop-filter: blur(5px);
  height: 25vh;
  width: 30vw;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  letter-spacing: 2.5px;
  transition: transform 0.3s ease-in-out;
}

main .placement:hover {
  transform: translate(-50%, -50%) scale(1.05);
}

main .slogan h1 {
  text-transform: capitalize;
  color: #181818;
  text-align: center;
  font-size: 2.5rem;
}

/* Section newsletter */
.newsletter {
  width: 100%;
  height: 55vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 18rem;
  background: url("../Images/image3.jpg") center;
  background-attachment: fixed;
  background-size: cover;
}

.newsletter .container_letter {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.newsletter .container_letter h1 {
  color: #fcfcfe;
  font-size: 3rem;
}

.newsletter .container_letter form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  position: relative;
}

.newsletter .container_letter form input {
  width: 450px;
  height: 50px;
  padding: 15px;
  font-size: 1.3rem;
  outline: none;
  border: none;
  background: rgba(223, 223, 223, 0.123);
  box-shadow: 0 4px 30px rgba(68, 37, 20, 0.164);
  backdrop-filter: blur(3.7px);
}

.newsletter .container_letter form button {
  width: 200px;
  height: 50px;
  font-size: 20px;
  color: #fff;
  border: none;
  cursor: pointer;
  background-image: linear-gradient(to right, #2a067e 0%, #1b345a 51%, #ff512f 100%);
  background-size: 200% auto;
  transition: background-position 0.5s, box-shadow 0.3s;
}

.newsletter .container_letter form button:hover {
  background-position: right center;
  box-shadow: 0 0 20px #444;
}

/* Responsiveness pour mobile */
@media only screen and (max-device-width: 780px) {
  main {
    height: 60vh;
  }

  main .placement {
    width: 80%;
    height: 35%;
    letter-spacing: 1.25px;
  }

  main .slogan h1 {
    font-size: 1.75rem;
  }

  .newsletter {
    margin-top: 5rem;
    height: auto;
  }

  .newsletter .container_letter h1 {
    font-size: 1.5rem;
  }

  .newsletter .container_letter form input {
    width: 350px;
    font-size: 1rem;
  }

  .newsletter .container_letter form button {
    width: 150px;
  }

  .slide1 {
    background: linear-gradient(to bottom, rgba(117, 117, 117, 0.15), rgba(32, 32, 32, 0)),
      url("../Images/image2-re.jpg");
  }

  .slide2 {
    background: linear-gradient(to bottom, rgba(117, 117, 117, 0.15), rgba(32, 32, 32, 0)),
      url("../Images/image4-re.jpg");
  }

  .slide3 {
    background: linear-gradient(to bottom, rgba(117, 117, 117, 0.15), rgba(32, 32, 32, 0)),
      url("../Images/image5-re.jpg");
  }

